@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@700&display=swap);
.header{height:80px;background-color:var(--secondary-bg-color);display:flex;align-items:center;padding:0 20px}.header__title{font-size:var(--font-size-xl)}.logo{display:flex;align-items:center;justify-content:center}.logo p{margin-left:10px;font-size:var(--font-size-lg)}
.checkbox{width:25px;height:25px;border:2px solid var(--tertiary-border-color);display:flex;align-items:center;justify-content:center;border-radius:var(--primary-border-radius);margin-right:10px;cursor:pointer;transition:.5s}.checkbox svg{width:15px;opacity:0}.checkbox:hover{background-color:var(--tertiary-bg-color)}.checkbox:hover svg{opacity:1}.checkbox--active{background-color:var(--tertiary-bg-color)}.checkbox--active svg{opacity:1}
.todos{margin:20px 0;padding:0 10px;flex:1 1 auto;overflow-y:auto;height:100%}.todo{display:flex;align-items:center;background-color:var(--secondary-bg-color);margin-top:15px;padding:15px 10px;border-radius:var(--primary-border-radius)}.todo:first-child{margin-top:0}.todo__title{font-size:var(--font-size-md);flex:1 1 auto}.todo__button{margin-left:15px}.todo__button:hover path{fill:var(--primary-svg-color)}
.form{width:100%;padding:0 10px;position:relative;display:flex;align-items:center;z-index:1}.form__field{width:100%;border:2px solid var(--secondary-border-color);padding:10px 15px;border-radius:var(--primary-border-radius);font-size:var(--font-size-md)}.form__label{position:absolute;left:20px;z-index:-1;transition:.5s;font-size:var(--font-size-md);background-color:var(--primary-bg-color)}.form__field:not(:-ms-input-placeholder).form__field:not(:focus)~.form__label{z-index:1;transform:translateY(-150%);font-size:var(--font-size-sm)}.form__field:focus~.form__label,.form__field:not(:placeholder-shown).form__field:not(:focus)~.form__label{z-index:1;transform:translateY(-150%);font-size:var(--font-size-sm)}
.board{width:100%;display:flex;flex-direction:column;height:100vh;padding-bottom:10px}
.folders{margin:10px 0;flex:1 1 auto;height:100%;overflow-y:auto}.folder{padding:15px 10px;background-color:var(--primary-bg-color);margin-top:10px;border-radius:var(--primary-border-radius);position:relative;display:flex;align-items:center;cursor:pointer}.folder::before{position:absolute;content:"";width:10px;height:10px;background-color:var(--tertiary-bg-color);right:10px;border-radius:50%;opacity:0;transition:.5s}.folder:hover::before{opacity:1}.folder__title{font-size:var(--font-size-md)}
.aside{height:100vh;display:flex;flex-direction:column;min-width:320px;background-color:var(--secondary-bg-color);padding:10px 10px}.aside__field .form__field{border-color:var(--primary-border-color)}.aside__field .form__label{background-color:var(--secondary-bg-color)}
.main{display:flex}
:root{--primary-text-color: #FFFFFF;--primary-bg-color: #212832;--secondary-bg-color: #2C343F;--tertiary-bg-color: #543FD3;--font-size-xl: 36px;--font-size-lg: 24px;--font-size-md: 18px;--font-size-sm: 14px;--primary-border-radius: 10px;--secondary-border-radius: 20px;--primary-border-color: #212832;--secondary-border-color: #2C343F;--tertiary-border-color: #543FD3;--primary-svg-color: #FFFFFF;--secondary-svg-color: #2C343F}*{margin:0;padding:0;box-sizing:border-box;font-family:"Comfortaa",cursive}*::-webkit-scrollbar{width:0}body{height:100%;background-color:#212832;background-color:var(--primary-bg-color);color:#FFFFFF;color:var(--primary-text-color)}button,input{background:none;border:none;outline:none;cursor:pointer;color:#FFFFFF;color:var(--primary-text-color);transition:.5s}input:-ms-input-placeholder{color:#FFFFFF;color:var(--primary-text-color);font-size:14px;font-size:var(--font-size-sm)}input::placeholder{color:#FFFFFF;color:var(--primary-text-color);font-size:14px;font-size:var(--font-size-sm)}svg,path{transition:.5s}
